<template>
  <div class="video-wrapper">
    <video
      ref="video"
      controls
      autoplay
      muted
      playsinline
      class="video-element"
    ></video>
  </div>
</template>

<script>
import Hls from "hls.js";

export default {
  name: "CCTVPlayer",
  props: {
    videoUrl: {
      type: String,
      required: true
    }
  },
  data() {
    return {
      hls: null
    };
  },
  watch: {
    //  videoUrl 
    videoUrl(newUrl) {
      if (newUrl) {
        this.initHls(newUrl);
      }
    }
  },
  mounted() {
    this.initHls(this.videoUrl);
  },
  methods: {
    initHls(url) {
      const video = this.$refs.video;

      // 
      if (this.hls) {
        this.hls.destroy();
      }

      if (Hls.isSupported()) {
        this.hls = new Hls();
        this.hls.loadSource(url);
        this.hls.attachMedia(video);
        this.hls.on(Hls.Events.MANIFEST_PARSED, () => {
          video.play().catch(e => console.warn("Autoplay blocked", e));
        });
      } else if (video.canPlayType("application/vnd.apple.mpegurl")) {
        video.src = url;
      }
    }
  },
  beforeDestroy() {
    if (this.hls) {
      this.hls.destroy();
    }
  }
};
</script>

<style scoped>
.video-wrapper {
  background: #000;
  border-radius: 8px;
  overflow: hidden;
  width: 100%;
}
.video-element {
  width: 100%;
  display: block;
}
</style>
